module.exports = "/* HEADER */\n\nheader {\n  background-color: var(--n-dark);\n}\n\nheader h1 {\n  color: var(--text-inv);\n  background-color: var(--n-dark);\n  padding: 1.1rem 0 0 45px;\n  margin-left: 5%;\n}\n\nh1 img {\n  max-width: 175px;\n}\n\nheader > button {\n  position: absolute;\n  top: 0;\n  left: 5%;\n  background: initial;\n  padding: 1rem;\n  margin-left: -1rem;\n  transition: -webkit-transform 0.7s;\n  transition: transform 0.7s;\n  transition: transform 0.7s, -webkit-transform 0.7s;\n}\n\nheader > button img {\n  width: 25px;\n  height: 25px;\n}\n\n/* If Burger Menu Open */\n\n.mobileOpen {\n  height: 100%;\n  left: 0;\n  transition: left 0.6s;\n}\n\n/* Else if Burger Menu Close */\n\n.mobileClose {\n  transition: left 0.6s;\n}\n\n/* Rotate Burger Button */\n\n.burgerVertical {\n  -webkit-transform: rotateZ(90deg);\n          transform: rotateZ(90deg);\n}\n\napp-menu {\n  position: absolute;\n  left: -1000px;\n  width: 80%;\n  max-width: 350px;\n  height: auto;\n  color: var(--text-inv);\n  background-color: var(--n-dark);\n  transition: left 0.6s;\n  z-index: 999;\n}\n\n@media screen and (min-width: 1000px) {\n  header {\n    height: 100%;\n  }\n  header .logo {\n    display: block;\n    text-align: center;\n    padding: 5vh 0 9vh;\n  }\n  header h1 {\n    padding: 0;\n    margin: 0;\n  }\n  header > button {\n    display: none;\n  }\n  header > p {\n    padding: 0;\n  }\n\n  header .mobileClose,\n  .mobileOpen {\n    left: 0;\n    height: auto;\n    background-color: var(--n-dark);\n  }\n\n  app-menu {\n    display: block;\n    position: static;\n    overflow: hidden;\n    width: auto;\n    height: auto;\n    top: 0;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"